<template>
<!--  -->
<Frame title="">
  <div class="mui-container">
  <p class="info text-center"></p>
  <hr/>
  <div class="adv-btns">
    <button class="mui-btn mui-btn-block mui-btn-royal" @click="showAd('YOUMI')"></button>
    <button class="mui-btn mui-btn-block mui-btn-yellow" @click="showAd('WAPS')"></button>
  </div>
  <div class="tips">
    <ul class="mui-table-view"> 
      <li class="mui-table-view-cell mui-collapse mui-active bc-white">
        <a href="#"></a>
        <div class="mui-collapse-content">
          <b>35~200</b>
        </div>
      </li>
      <li class="mui-table-view-cell mui-collapse mui-active bc-white">
          <a href="#"></a>
          <div class="mui-collapse-content">
            <ul style="color: red">
              <li></li>
              <br/>
              <li></li>
              <br/>
              <li></li>
            </ul>
          </div>
        </li>
    </ul>
  </div>
  
  </div>
</Frame>
</template>
<script>
import $ from 'axios';
import {mapState} from 'vuex';
import {user as USER_API} from '@/config/serverApi';
import Frame from '@/components/Frame.vue';
export default {
  components: {
    Frame
  },
  data() {
    return {
      qq: '',
      email: '',
      mobile: '',
      reg_time: ''
    };
  },
  computed: {
     ...mapState({
       user: state => state.user
     })
   },
  methods: {
    showAd(channel) {
      window.zanhuang && window.zanhuang.showAd(channel, 0, this.user.user || 'telanx');
    }
  },
  mounted() {
    
  }
}
</script>
<style>
.info {
  background: #fff;
  padding: 1rem;
  font-size: 0.8rem;
}
.tips {
  background: #fff;
  padding: 10px 0;
  font-size: 0.45rem;
}
</style>


