<script>
export default {
  data() {
    return {
      form: {
        weibo_url: '',
        remark: ''
      }
    }
  },
  methods: {
    checkFormData() {
      const weibo_url = this.form.weibo_url;
      if (weibo_url.trim() === '') {
        this.$tip.show('~');
